          "classifyEdit",
          "member",
          "memberGoods",
          "memberUser",
          "memberGoodsAdd",
          "memberGoodsEdit",
          "keyword",
          "keywordAdd",
          "keywordEdit",
          "keywordDel",
          "activity",
          "activityAdd",
          "kactivityEdit",
          "user",
          "attache",
          "apply",
          "attacheAudit",
          "attacheUnAudit",
          "underling",
          "commission",
          "qrcode",
          "emptyMoney",
          "expert",
          "expertEdit",
          "expertAdd",
          "expertDel",
          "order",
          "commonOrder",
          "memberOrder",
          "expressage",
          "logisticsAdd",
          "logisticsEidt",
          "logisticsDel",
          "distribution",
          "logisticsGood",
          "logisticsGoodMer",
          "orderGood",
          "sendGood",
          "distributionMer",
          "sendGoodMer",

          "information",
          "banner",
          "message",
          "classify",
          'classifyGoodAdd',
          'classifyGoodEdit',
          'classifyGoodDel'

        ],
        permissionList: [
          "permission", // 
          "roles",
          "manage",
          "healthUser",
          "roleAdd",
          "roleDel",
          "roleEdit",
          "roleGet",
          "manageAdd",
          "manageDel",
          "manageEdit",
          "manageResetPwd",
          "healthUserEdit",
