          "merchantEdit",
          "comSource",
          "commodity",
          "commodityList",
          "commodityAdd",
          "commodityEdit",
          "classifyEdit",
          "member",
          "memberGoods",
          "memberUser",
          "memberGoodsAdd",
          "memberGoodsEdit",
          "keyword",
          "keywordAdd",
          "keywordEdit",
          "keywordDel",
          "activity",
          "activityAdd",
          "kactivityEdit",
          "attache",
          "apply",
          "withdraw",
          "auditWithdraw",
          "attacheAudit",
          "attacheUnAudit",
          "underling",
          "commission",
          "qrcode",
          "emptyMoney",
          "user",
          "expert",
          "expertEdit",
          "expertAdd",
          "expertDel",
          "order",
          "commonOrder",
          "memberOrder",
          "expressage",
          "logisticsAdd",
          "logisticsEidt",
          "logisticsDel",
          "distribution",
          "logisticsGood",
          "logisticsGoodMer",
          "orderGood",
          "sendGood",
          "distributionMer",
          "sendGoodMer",
          "classify",
          'classifyGoodAdd',
          'classifyGoodEdit',
          'classifyGoodDel',
        ],

        dialogDel: false,
        id: "", // id

        // 
        current: 1,
        size: 10,
        total: 0,
        role: localStorage.getItem("username"),
        permissionData: [],

        isAdmin: false
      };
    },
    mounted() {
      this.permissionData = localStorage.getItem("permissions").split(",");
      this.getRoles(this.current, this.size);
    },
    methods: {
      getRoles(cur, list) {
        var self = this;
        API.roles(cur, list)
          .then((res) => {
            self.loading = false;
            self.tableData = res.result.data;
            self.total = res.result.total;
          })
          .catch((err) => {
            self.loading = false;
          });
      },
      // 
      currentChange(val) {
        var self = this;
        self.current = val;
        self.loading = true;
        self.getRoles(val, self.size);
      },
      // 
      sizeChange(val) {
        var self = this;
        self.size = val;
        self.loading = true;
        self.getRoles(1, val);
        self.current = 1;
      },
      newRole() {
        var self = this;
        if (self.form.name && self.form.permissions.length > 0) {
          API.createRole(self.form).then((res) => {
            self.dialogRole = false;
            self.$message.success("");
            self.getRoles(self.current, self.size);
            self.form = {};
            self.form.permissions = [];
          });
        } else {
          self.$message.warning("");
        }
      },
      // 
      handleEdit(index, row) {
        var self = this;
        if (self.permissionData.includes("roleEdit")) {
          self.dialogRole = true;
        } else {
          self.$message.warning("");
        }
        if (row.type == 0) {
          self.isAdmin = true;
          self.checkAll = row.permissions.length >= self.adminPermission.length;
        } else {
          self.isAdmin = false;
          self.checkAll = row.permissions.length >= self.permissionList.length;
        }
        console.log(row);
        self.disabledRole = true;
        self.form.title = row.title;
        self.form.name = row.name;
        self.form.id = row.id;
        self.form.permissions = row.permissions;
        if (self.checkAll === true) {
          if (self.isAdmin) {
            self.form.permissions = self.checkAll ? self.adminPermission : [];
          } else {
            self.form.permissions = self.checkAll ? self.permissionList : [];
          }
        }
      },
      close() {
        var self = this;
        self.form = {
          name: "",
          id: "",
          permissions: [],
          title: ''
        };
      },
      AllChange(val) {
        var self = this;
        console.log(self.isAdmin);
        if (self.isAdmin) {
          self.form.permissions = val ? self.adminPermission : [];
        } else {
          self.form.permissions = val ? self.permissionList : [];
        }
      },
      oneChange(val) {
        var self = this;
        if (self.isAdmin) {
          self.checkAll = self.form.permissions.length >= self.adminPermission.length;
        } else {
          self.checkAll = self.form.permissions.length >= self.permissionList.length;
        }
      },

      handleDel(index, row) {
        var self = this;
        if (self.permissionData.includes("roleDel")) {
          self.dialogDel = true;
          self.id = row.id;
        } else {
          self.$message.warning("");
        }
      },
      toDel() {
        var self = this;
        API.delRoles(self.id).then((res) => {
          self.$message.success("");
          self.dialogDel = false;
          self.getRoles(self.current, self.size);
        });
      },
    },
  };
</script>

